export default {
	data() {
		return {
			openId: '',
			imgUrl: '',
			avatarUrl: '',
			name: ''
		};
	},
	onReady() {
		uni.getUserInfo({
			success: res => {
				this.name = res.userInfo.nickName;
				this.avatarUrl = res.userInfo.avatarUrl;
				uni.getStorage({
					key: 'openId',
					success: res => {
						this.openId = res.data;
						uni.showLoading({
							title: '读取数据中...',
							mask: false
						});
						this.requestUrl();
					}
				});
			}
		});
	},
	onLoad() {
		uni.getUserInfo({
			success: res => {
				this.name = res.userInfo.nickName;
				this.avatarUrl = res.userInfo.avatarUrl;
				uni.getStorage({
					key: 'openId',
					success: res => {
						this.openId = res.data;
						uni.showLoading({
							title: '读取数据中...',
							mask: false
						});
						this.requestUrl();
					}
				});
			}
		});
	},
	methods: {
		onRefresh() {
			this.requestUrl();
		},
		requestUrl() {
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/common/getQRcode',
				method: 'POST',
				header: {
					'content-Type': 'application/json'
				},
				data: {
					openid: this.openId
				},
				success: res => {
					uni.hideLoading();
					this.imgUrl = res.data.data.success
					console.log(res.data);
				},
				fail: err => {
					console.log(err);
				}
			});
		},
		onName() {
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/shopInfo/getHeadPortraitAndName',
				method: 'POST',
				header: {
					'content-Type': 'application/x-www-form-urlencoded'
				},
				data: {
					openid: this.openId
				},
				success: res => {
					console.log(res.data.data);
					this.name = res.data.data.name;
					this.avatarUrl = res.data.data.head_portrait;
				}
			});
		},
		photoSave() {
			uni.saveImageToPhotosAlbum({
				filePath: this.imgUrl,
				success: res => {
					console.log('save success');
				}
			});
		}
	}
}
